      if (n.value > 90) {
        i.style.color = "#ecdab5";
      } else if (n.value > 85) {
        i.style.color = "#e85160";
      } else if (n.value > 70) {
        i.style.color = "#00a2f5";
      }
    },
  },
  methods: {
    textfun(num) {
      if (num > 90) {
        return "〖神作〗";
      } else if (num > 85) {
        return "好评如潮";
      } else if (num > 80) {
        return "脍炙人口";
      } else if (num > 70) {
        return "值得一读";
      }
      // return
    },
    handleScroll() {
      const windowHeight = window.innerHeight; // 浏览器窗口高度
      const documentHeight = document.documentElement.scrollHeight; // 页面总高度
      const scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop; // 滚动距离

      if (scrollTop + windowHeight >= documentHeight) {
        // 滑动到页面底部
        setTimeout(() => {
          this.flag = true;
        }, 300);
        setTimeout(() => {
          this.flag = false;
          this.Arr.forEach((item) => {
            this.bookArr.push(item);
          });
        }, 1500);
        // 执行你想要的操作...
      }
    },
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
  },
