<template>
  <div id="box">
    <soubox></soubox>
    <div class="box_nav">
      <span @click="fun(1)" :class="flag == 'one' ? 'active' : ''">推荐</span>
      <span @click="fun(2)" :class="flag == 'two' ? 'active' : ''">分类</span>
      <span @click="fun(3)" :class="flag == 'three' ? 'active' : ''">排行</span>
      <span @click="fun(4)" :class="flag == 'four' ? 'active' : ''"
        >文学艺术</span
      >
      <span @click="fun(5)" :class="flag == 'five' ? 'active' : ''"
        >哲学社科</span
      >
    </div>
    <components :is="tab"></components>
  </div>
</template>

<script>
import soubox from "../components/SouBox.vue";
import one from "../components/books/OneView.vue";
import two from "../components/books/TwoView.vue";
import three from "../components/books/ThreeView.vue";
import four from "../components/books/FourView.vue";
import five from "../components/books/FiveView.vue";
export default {
  data() {
    return {
      tab: "one",
      flag: "one",
    };
  },
  methods: {
    fun(num) {
      if (num == 1) {
        this.tab = "one";
        this.flag = "one";
      } else if (num == 2) {
        this.tab = "two";
        this.flag = "two";
      } else if (num == 3) {
        this.tab = "three";
        this.flag = "three";
      } else if (num == 4) {
        this.tab = "four";
        this.flag = "four";
      } else if (num == 5) {
        this.tab = "five";
        this.flag = "five";
      }
    },
  },
  components: {
    soubox,
    one,
    two,
    three,
    four,
    five,
  },
};
</script>

<style lang="scss" scoped>
#box {
  width: 100vw;
  //   height: 1000px;
  //   border-radius: 6px;

  .box_nav {
    margin-top: 15px;
    width: 100%;
    height: 40px;
    background: white;
    display: flex;
    justify-content: space-between;
    color: #7f8494;
    font-family: "Courier New", Courier, monospace;
    font-weight: 900;
    position: sticky;
    top: 107px;
    z-index: 999999;
    span {
      display: block;
      padding: 0 4%;
      //   width: 20%;
      height: 100%;
      line-height: 40px;
      text-align: center;
      font-size: 13px;
    }
    .active {
      color: #2f9aed !important;
    }
  }
}
</style>
